#Header {
  z-index: 1500;
  width: 100%;
  background: black;
  background-repeat: no-repeat;
  padding: 30px 0px 30px 0px;
}
@font-face {
  font-family: "Anirm";
  src: url("../../fonts/Anirm/anirm___.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}
@media screen and (max-width: 995px) {
  #Header {
    display: flex;
    justify-content: flex-start;
    align-items: center;
  }
}
#Header.active {
  position: fixed;
}
#menu__toggle {
  opacity: 0;
}
#menu__toggle:checked ~ .menu__btn > span {
  transform: rotate(45deg);
}
#menu__toggle:checked ~ .menu__btn > span::before {
  top: 0;
  transform: rotate(0);
}
#menu__toggle:checked ~ .menu__btn > span::after {
  top: 0;
  transform: rotate(90deg);
}
#menu__toggle:checked ~ .menu__box,
#menu__toggle:checked ~ .menu__box > .header-item-wrapper {
  visibility: visible;
  left: 0;
}
.menu__btn {
  display: none;
}
@media screen and (max-width: 995px) {
  .menu__btn {
    z-index: 1600;
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 40px;
    right: 20px;
    width: 40px;
    height: 40px;
    cursor: pointer;
    border: 1px solid black;
    border-radius: 2px;
    background: rgba(0, 0, 0, 0.9);
  }
}
#Header .menu__btn > span,
#Header .menu__btn > span::before,
#Header .menu__btn > span::after {
  display: block;
  position: absolute;
  width: 20px;
  height: 1px;
  background-color: white;
  transition-duration: 0.25s;
}
#Header .menu__btn > span::before {
  content: "";
  top: -6px;
}
#Header .menu__btn > span::after {
  content: "";
  top: 6px;
}
#Header .hamburger-menu {
  max-width: 1400px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.menu__btn-active {
  background: red;
  width: 100px;
  height: 100px;
}
#Header .logo_995px {
  padding-left: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  text-decoration-line: none;
}

#Header .logo_wrapper {
  text-decoration: none;
}

#Header .logo_995px .logo_img {
  width: 45px;
  height: 45px;
  background: url(img/Безымянный-1.png) center/cover;
  background-repeat: no-repeat;
}
#Header .logo_995px .logo_text {
  font-weight: 100;
  margin-left: 5px;
  line-height: 1.2;
  color: white;
  font-family: "Anirm";
}
#Header .menu__box {
  z-index: 1500;
  padding-top: 0px;
  display: flex;
  padding-right: 50px;
}
#Header .header-item-wrapper {
  margin-top: 10px;
  text-decoration: none;
  display: block;
  font-family: "OpenSans", sans-serif;
  padding: 5px 25px;
  color: white;
  font-size: 20px;
  line-height: 28px;
  font-weight: 100;
}
#Header .menu__item {
  padding-bottom: 5px;
  border-bottom: 2px solid black;
}

#Header .menu_items {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-bottom: 25px;
  padding-left: 15px;
}
#Header .menu__item:hover {
  border-bottom: 2px solid red;
  transition: 0.65s ease;
}
#Header .soc_link {
  text-align: right;
  font-weight: 400;
}
@media screen and (max-width: 995px) {
  #Header .menu__box {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: fixed;
    visibility: hidden;
    top: 0px;
    left: -100%;
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 30px 0;
    list-style: none;
    background: rgba(0, 0, 0, 0.9);
    transition-duration: 0.25s;
  }
  #Header .hamburger-menu {
    display: flex;
    justify-content: center;
  }
  #Header .menu_items {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  #Header .menu__item {
    display: block;
    padding: 25px 24px;
    color: white;
    font-size: 35px;
    font-weight: 600;
    text-decoration: none;
  }
  #Header .menu__item1 {
    margin-left: 0px;
  }
  #Header .menu__item:hover {
    color: white;
  }
  #Header .soc_link {
    text-align: center;
    font-weight: 400;
    font-size: 18px;
  }
  #Header .logo_995px {
    margin-top: 0px;
  }
  #Header .logo_995px .logo_img {
    width: 56px;
    height: 56px;
    background: url(img/Безымянный-1.png) center/cover;
    background-repeat: no-repeat;
  }
  #Header .logo_995px .logo_text {
    width: 236px;
    height: 36px;
  }
}

@media screen and (max-width: 430px) {
  #Header .logo_995px .logo_text {
    font-size: 20px;
    max-width: 150px;
  }
}
