@keyframes leftAnimation {
  0% {
    opacity: 0;
    left: -150px;
  }
  100% {
    opacity: 1;
    left: 0px;
  }
}
@keyframes rightAnimation {
  0% {
    right: -150px;
    opacity: 0;
  }
  100% {
    opacity: 1;
    right: 0px;
  }
}
@font-face {
  font-family: "Anirm";
  src: url("../../fonts/Anirm/anirm___.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}
.ParticularHero_conatiner {
  padding-top: 90px;
  max-width: 1400px;
  margin: 0 auto;
  display: flex;
  justify-content: center;
}

.box_right {
  position: relative;
  animation: rightAnimation 2s ease;
  animation-fill-mode: forwards;
}
.box_left {
  position: relative;
  animation: leftAnimation 2s ease;
  animation-fill-mode: forwards;
}
.ParticularHero_img {
  padding: 10px 10px 10px 10px;
  background: url(img/a292e6b0786a581e9b5c180e32072a56.jpg) center/cover;
  background-repeat: no-repeat;
  border-left: 1px solid white;
  border-top: 1px solid white;
  border-bottom: 1px solid white;
  border-bottom-left-radius: 5px;
  border-top-left-radius: 5px;
  z-index: 500;
  position: relative;
  width: 30%;
  height: 400px;
}
.particular-hero-wrapper {
  position: relative;
  width: 100%;
  height: 300px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.particular-hero-name {
  margin: 10px 10px;
  font-weight: 600;
  font-size: 28px;
  font-family: "Anirm";
}
.particular-hero-text {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  margin-top: 20px;
}
.particular-hero-text p {
  margin-top: 15px;
  padding-left: 10px;
  max-width: 400px;
}
.particular-hero-text span {
  font-weight: 600;
}
.particular-hero-link {
  margin: 0 auto;
  width: 150px;
  height: 50px;
  cursor: pointer;

  text-decoration: none;
  background: gray;
  border: 1px solid white;
  border-radius: 5px;
}
.particular-hero-link:hover {
  background: black;
  border: 1px solid white;
  transition: 0.85s ease;
}
.particular-hero-link:hover a {
  color: white;
}
.particular-hero-link p {
  text-align: center;
  margin-top: 15px;
}
.particular-hero-link a {
  text-decoration: none;
  color: white;
}

.ParticularHero_info {
  margin: 0;
  width: 40%;
  background-color: rgba(255, 255, 255, 0.6);
  border-right: 1px solid white;
  border-top: 1px solid white;
  border-bottom: 1px solid white;
  border-bottom-right-radius: 5px;
  border-top-right-radius: 5px;
}

@media screen and (max-width: 900px) {
  .ParticularHero_conatiner {
    padding-top: 0px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .particular-hero-wrapper {
    position: relative;
    width: 100%;
    height: 400px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .ParticularHero_img {
    width: 80%;
    height: 300px;
    border-left: 1px solid white;
    border-right: 1px solid white;
    border-top: 1px solid white;
    border-bottom: none;
    border-bottom-left-radius: 0px;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
  }
  .ParticularHero_info {
    width: 80%;
    border-right: 1px solid white;
    border-top-right-radius: 0px;
    border-top: none;
    border-bottom: 1px solid white;
    border-bottom-right-radius: 5px;
    border-bottom-left-radius: 5px;
  }
  .particular-hero-link {
    padding-bottom: 20px;
    margin-bottom: 20px;
  }
}
@media screen and (max-width: 760px) {
  .ParticularHero_img {
    width: 80%;
    height: 400px;
  }
  .particular-hero-wrapper {
    height: 350px;
  }
}
